import React from 'react';
import { Container, Row, Col, Image, Card } from 'react-bootstrap';

const Panel = ({ data }) => {
  if (!data) return null;
  const { title, items } = data;
  return (
    <Container as="section" id="panel" className="py-5">
      <Card className="content-card shadow-sm">
        <Card.Body>
          <h2 className="text-center mb-5">{title}</h2>
          <Row className="justify-content-center align-items-center g-4 pb-4">
            {items.map((item, index) => (
              <Col key={index} xs={6} sm={4} md={3} lg={2} className="text-center panel-item">
                <Image
                  src={require(`../assets/images/panel/${item.image}`)}
                  alt={`${item.name} logo`}
                  fluid
                  style={{ maxHeight: '80px', objectFit: 'contain' }}
                />
              </Col>
            ))}
          </Row>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default Panel; 