import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';

const Services = ({ data }) => {
  if (!data) return null;
  const { title, items } = data;

  return (
    <Container as="section" id="services" className="py-5">
      <Card className="content-card shadow-sm">
        <Card.Body>
          <h2 className="text-center mb-5">{title}</h2>
          <Row xs={1} md={2} lg={4} className="g-4 justify-content-center">
            {items.map((service, index) => (
              <Col key={index} className="d-flex align-items-stretch">
                <Card className="text-center h-100 border-0 shadow-sm service-item-card">
                  <Card.Body className="d-flex flex-column">
                    <div className="mb-3" style={{ fontSize: '3rem', color: 'var(--kenanga-teal)' }}>
                      <i className={service.icon}></i>
                    </div>
                    <Card.Title as="h5" className="mb-3">{service.name}</Card.Title>
                    <Card.Text className="flex-grow-1">{service.description}</Card.Text>
                  </Card.Body>
                </Card>
              </Col>
            ))}
          </Row>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default Services; 