import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';

const Doctors = ({ data }) => {
  if (!data) return null;
  const { title, items } = data;

  return (
    <Container as="section" id="doctors" className="py-5">
      <Card className="content-card shadow-sm">
        <Card.Body>
          <h2 className="text-center mb-5">{title}</h2>
          <Row className="justify-content-center">
            {items.map((doctor, index) => (
              <Col md={4} key={index} className="mb-4 d-flex align-items-stretch">
                <Card className="text-center h-100 border-0 shadow-sm service-item-card">
                  <div className="doctor-photo-container">
                    <Card.Img 
                      variant="top" 
                      src={require(`../assets/images/${doctor.image}`)} 
                      className="doctor-photo"
                    />
                  </div>
                  <Card.Body>
                    <Card.Title>{doctor.name}</Card.Title>
                    <Card.Text className="text-muted">{doctor.qualification}</Card.Text>
                    <Card.Text className="px-3">{doctor.profile}</Card.Text>
                  </Card.Body>
                </Card>
              </Col>
            ))}
          </Row>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default Doctors; 