import matter from 'gray-matter';
import content from './content.md';

let parsedContent;

// Since fetch is async, we need to handle the promise
const fetchAndParseContent = async () => {
  if (parsedContent) {
    return parsedContent;
  }

  const response = await fetch(content);
  const markdown = await response.text();
  const { data } = matter(markdown);
  parsedContent = data;
  return data;
};

export const getContent = async () => {
  try {
    return await fetchAndParseContent();
  } catch (error) {
    console.error("Failed to load or parse content:", error);
    // Return an empty object structure to prevent crashes
    return { site: {}, hero: {}, services: {}, doctors: {}, about: {}, contact: {}, panel: {} };
  }
}; 