import React from 'react';
import { Navbar, Nav, Container, Button } from 'react-bootstrap';
import { useLocation, useNavigate } from 'react-router-dom';
import '../App.css';

const Header = ({ activeLink, siteData }) => {
  const location = useLocation();
  const navigate = useNavigate();

  if (!siteData) return null;
  
  const { name, navbar_logo, links } = siteData;

  const handleHomeClick = (e) => {
    e.preventDefault();
    if (location.pathname === '/') {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    } else {
      navigate('/');
    }
  };

  return (
    <Navbar expand="lg" fixed="top" variant="dark">
      <Container>
        <Navbar.Brand href="#home" onClick={handleHomeClick} className="navbar-brand-logo">
          <img
            src={require(`../assets/images/${navbar_logo}`)}
            style={{ maxHeight: '3rem' }}
            className="d-inline-block align-top"
            alt={`${name} logo`}
          />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="/" onClick={handleHomeClick} active={activeLink === 'home'}>Home</Nav.Link>
            <Nav.Link href="#about" active={activeLink === 'about'}>About Us</Nav.Link>
            <Nav.Link href="#services" active={activeLink === 'services'}>Our Services</Nav.Link>
            <Nav.Link href="#panel" active={activeLink === 'panel'}>Our Panels</Nav.Link>
            <Nav.Link href="#doctors" active={activeLink === 'doctors'}>Our Doctors</Nav.Link>
            <Nav.Link href="#contact" active={activeLink === 'contact'}>Contact Us</Nav.Link>
            <Nav.Link href="#review" active={activeLink === 'review'}>Google Reviews</Nav.Link>
          </Nav>
          <Nav className="d-none d-lg-flex">
            <Button href={links.whatsapp} target="_blank" className="action-button me-2 d-flex align-items-center whatsapp-button">
                <i className="fab fa-whatsapp fa-lg"></i>
                <span style={{paddingLeft:'5px'}}>WhatsApp</span>
            </Button>
            <Button href={links.waze} target="_blank" className="action-button me-2 d-flex align-items-center waze-button">
                <i className="fab fa-waze fa-lg"></i>
                <span style={{paddingLeft:'5px'}}>Waze</span>
            </Button>
            <Button href={links.google_maps_url} target="_blank" className="action-button d-flex align-items-center gmap-button">
                <i className="fab fa-google fa-lg"></i>
                <span style={{paddingLeft:'5px'}}>Google Maps</span>
            </Button>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header; 