import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';

const Footer = ({ siteData, contactData }) => {
  if (!siteData || !contactData) return null;

  return (
    <footer className="site-footer text-light py-5">
      <Container>
        <Row>
          <Col md={4} className="mb-4 mb-md-0">
            <h5>{siteData.name}</h5>
            <p className="text-white-50">{siteData.description}</p>
          </Col>
          <Col md={2} className="mb-4 mb-md-0">
            <h5>Quick Links</h5>
            <ul className="list-unstyled">
              <li><a href="#home" className="footer-link">Home</a></li>
              <li><a href="#services" className="footer-link">Services</a></li>
              <li><a href="#panel" className="footer-link">Panels</a></li>
              <li><a href="#contact" className="footer-link">Contact</a></li>
            </ul>
          </Col>
          <Col md={3} className="mb-4 mb-md-0">
            <h5>Contact Us</h5>
            <ul className="list-unstyled">
              <li className="d-flex mb-2">
                <i className="fas fa-map-marker-alt fa-fw me-2 mt-1"></i>
                <span className="text-white-50">{contactData.address}</span>
              </li>
              <li className="d-flex mb-2">
                <i className="fas fa-phone fa-fw me-2 mt-1"></i>
                <span className="text-white-50">{contactData.phone}</span>
              </li>
              <li className="d-flex mb-2">
                <i className="fas fa-envelope fa-fw me-2 mt-1"></i>
                <span className="text-white-50">{contactData.email}</span>
              </li>
            </ul>
          </Col>
          <Col md={3}>
            <h5>Get In Touch</h5>
            <p className="text-white-50">Use our quick links to navigate or get in touch with us directly.</p>
            {/* You could add social media icons here if you have them */}
          </Col>
        </Row>
        <hr className="mt-5 mb-4" />
        <div className="text-center text-white-50">
          &copy; {new Date().getFullYear()} {siteData.name}. All Rights Reserved.
        </div>
      </Container>
    </footer>
  );
};

export default Footer; 