import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { FaStar } from 'react-icons/fa';
import './ReviewPage.css';

const RATING_TEXT = {
    0: "Your feedback is valuable to us.",
    1: "Oh no! We're sorry to hear that.",
    2: "We value your feedback.",
    3: "Thanks for sharing your thoughts.",
    4: "Glad you had a good experience!",
    5: "Excellent! Thank you for your feedback."
};

const ReviewPage = ({ googleReviewUrl, internalReviewUrl, siteData }) => {
    const [rating, setRating] = useState(0);
    const [hover, setHover] = useState(0);
    const [view, setView] = useState('rating'); // 'rating', 'internal_form'
    const [isExiting, setIsExiting] = useState(false);

    const displayedRating = hover || rating;

    useEffect(() => {
        if (view !== 'rating') return;

        const intervalId = setInterval(() => {
            const reviewLink = document.querySelector('a[href*="elfsight.com"]');
            if (reviewLink) {
                reviewLink.style.setProperty('display', 'none', 'important');
            }
        }, 50);

        return () => clearInterval(intervalId);
    }, [view]);

    const handleRatingClick = (rate) => {
        setRating(rate);
        if (rate === 5) {
            window.location.href = googleReviewUrl;
        } else {
            setIsExiting(true);
            setTimeout(() => {
                setView('internal_form');
            }, 500); // Match animation duration
        }
    };

    const renderRatingView = () => (
        <Card className={`text-center shadow-lg animate__animated ${isExiting ? 'animate__fadeOut' : 'animate__fadeIn'}`}>
            {siteData && (
                <img
                    src={require(`../assets/images/${siteData.navbar_logo}`)}
                    alt={`${siteData.name} logo`}
                    className="card-img-top review-card-logo mx-auto mt-4"
                />
            )}
            <Card.Body>
                <Card.Title as="h2" className="mb-3">
                    Share Your Experience
                </Card.Title>
                <div className="star-rating mb-2">
                    {[...Array(5)].map((_, index) => {
                        const ratingValue = index + 1;
                        return (
                            <label key={index}>
                                <input
                                    type="radio"
                                    name="rating"
                                    value={ratingValue}
                                    onClick={() => handleRatingClick(ratingValue)}
                                />
                                <FaStar
                                    className={`star ${ratingValue === 5 ? 'star-five' : ''}`}
                                    color={ratingValue <= displayedRating ? '#ffc107' : '#e4e5e9'}
                                    size={40}
                                    onMouseEnter={() => setHover(ratingValue)}
                                    onMouseLeave={() => setHover(0)}
                                />
                            </label>
                        );
                    })}
                </div>
                <div className="rating-feedback-text mb-4">
                    {RATING_TEXT[displayedRating]}
                </div>
                <hr />
                <div className="social-proof-section mt-4">
                    <h3 className="h5 mb-3 text-muted">Join Our Happy Patients</h3>
                    <div className="elfsight-app-8c9274b6-c91c-432d-b861-ae87c74cf904" data-elfsight-app-lazy></div>
                </div>
            </Card.Body>
        </Card>
    );

    const renderInternalFormView = () => (
        <Card className="text-center shadow-lg animate__animated animate__fadeInUp">
            {siteData && (
                 <img
                    src={require(`../assets/images/${siteData.navbar_logo}`)}
                    alt={`${siteData.name} logo`}
                    className="card-img-top review-card-logo mx-auto mt-4"
                />
            )}
            <Card.Body>
                <Card.Title as="h2" className="mb-3">We're Sorry</Card.Title>
                <p className="lead">Your feedback is confidential and important to us. Please let us know how we can improve.</p>
                <div className="internal-review-form">
                    <iframe
                        src={internalReviewUrl}
                        width="100%"
                        height="1200"
                        frameBorder="0"
                        marginHeight="0"
                        marginWidth="0"
                        title="Internal Feedback Form"
                    >
                        Loading…
                    </iframe>
                </div>
            </Card.Body>
        </Card>
    );

    return (
        <div className="review-page-wrapper">
            <div className="review-page-container">
                <Container>
                    <Row className="justify-content-center">
                        <Col md={8} lg={6}>
                            {view === 'rating' ? renderRatingView() : renderInternalFormView()}
                        </Col>
                    </Row>
                </Container>
            </div>
        </div>
    );
};

export default ReviewPage;