import React, { useState, useEffect, useMemo } from 'react';
import { BrowserRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import { getContent } from './data/loader';
import Header from './components/Header';
import Footer from './components/Footer';
import Hero from './components/Hero';
import About from './components/About';
import Services from './components/Services';
import Doctors from './components/Doctors';
import Contact from './components/Contact';
import Panel from './components/Panel';
import Review from './components/Review';
import ReviewPage from './components/ReviewPage';
import BackToTopButton from './components/BackToTopButton';
import AnimatedSection from './components/AnimatedSection';
import AppSkeleton from './components/AppSkeleton';
import './App.css';

function App() {
  return (
    <Router>
      <AppContent />
    </Router>
  );
}

function AppContent() {
  const [data, setData] = useState(null);
  const [activeLink, setActiveLink] = useState('home');
  const [showBackToTop, setShowBackToTop] = useState(false);
  const location = useLocation();
  const isReviewPage = location.pathname === '/review';

  const sectionRefs = useMemo(() => ({
    home: React.createRef(),
    about: React.createRef(),
    services: React.createRef(),
    doctors: React.createRef(),
    panel: React.createRef(),
    contact: React.createRef(),
    review: React.createRef(),
  }), []);

  useEffect(() => {
    getContent().then((contentData) => {
      setData(contentData);
      if (contentData.site) {
        document.title = contentData.site.title;
        const metaDesc = document.querySelector('meta[name="description"]');
        if (metaDesc) {
          metaDesc.setAttribute('content', contentData.site.description);
        }
      }
    });
  }, []);

  useEffect(() => {
    const debounce = (func, delay) => {
      let timeout;
      return (...args) => {
        const context = this;
        clearTimeout(timeout);
        timeout = setTimeout(() => func.apply(context, args), delay);
      };
    };

    const handleScroll = () => {
      // Back to top button visibility
      if (window.scrollY > 300) {
        setShowBackToTop(true);
      } else {
        setShowBackToTop(false);
      }

      // Active link highlighting (scroll-spy)
      let currentActive = '';
      const sectionKeys = ['home', 'about', 'services', 'doctors', 'panel', 'contact', 'review'];
      for (const sectionName of sectionKeys) {
          const section = sectionRefs[sectionName].current;
          if (section && section.offsetTop <= window.scrollY + 150) {
              currentActive = sectionName;
          }
      }
      setActiveLink(currentActive || 'home');
    };

    const debouncedScrollHandler = debounce(handleScroll, 50);

    window.addEventListener('scroll', debouncedScrollHandler);
    return () => window.removeEventListener('scroll', debouncedScrollHandler);
  }, [sectionRefs, setActiveLink]);

  useEffect(() => {
    if (isReviewPage) {
      document.body.classList.add('review-page-active');
    } else {
      document.body.classList.remove('review-page-active');
    }
    // Cleanup on component unmount
    return () => {
      document.body.classList.remove('review-page-active');
    };
  }, [isReviewPage]);

  if (!data) {
    return <AppSkeleton />;
  }

  return (
    <>
      {!isReviewPage && data.site && <Header activeLink={activeLink} siteData={data.site} />}
      <main>
        <Routes>
          <Route path="/" element={
            <>
              <div ref={sectionRefs.home}>
                {data.hero && data.site?.links && <Hero data={{...data.hero, links: data.site.links}} />}
              </div>
              <div ref={sectionRefs.about}>{data.about && <AnimatedSection><About data={data.about} /></AnimatedSection>}</div>
              <div ref={sectionRefs.services}>{data.services && <AnimatedSection><Services data={data.services} /></AnimatedSection>}</div>
              <div ref={sectionRefs.panel}>{data.panel && <AnimatedSection><Panel data={data.panel} /></AnimatedSection>}</div>
              <div ref={sectionRefs.doctors}>{data.doctors && <AnimatedSection><Doctors data={data.doctors} /></AnimatedSection>}</div>
              <div ref={sectionRefs.contact}>{data.contact && data.site && <AnimatedSection><Contact data={data.contact} siteData={data.site} /></AnimatedSection>}</div>
              <div ref={sectionRefs.review}><AnimatedSection><Review /></AnimatedSection></div>
            </>
          } />
          <Route 
            path="/review" 
            element={
              data.site?.links && <ReviewPage 
                siteData={data.site}
                googleReviewUrl={data.site.links.google_review} 
                internalReviewUrl={data.site.links.internal_review_form} 
              />
            } 
          />
        </Routes>
      </main>
      {!isReviewPage && <BackToTopButton isVisible={showBackToTop} />}
      {!isReviewPage && data.site && data.contact && <Footer siteData={data.site} contactData={data.contact} />}
    </>
  );
}

export default App;
