import React, { useEffect } from 'react';
import { Container, Card } from 'react-bootstrap';

const Review = () => {
  useEffect(() => {
    const intervalId = setInterval(() => {
      const reviewLink = document.querySelector(
        'a[href*="elfsight.com/google-reviews-widget"]'
      );

      if (reviewLink) {
        // Continuously apply styles to win the "style war" against the widget.
        reviewLink.style.setProperty('display', 'none', 'important');
        reviewLink.style.setProperty('height', '0', 'important');
        reviewLink.style.setProperty('padding', '0', 'important');
        reviewLink.style.setProperty('margin', '0', 'important');
        reviewLink.style.setProperty('visibility', 'hidden', 'important');
        reviewLink.style.setProperty('opacity', '0', 'important');
      }
    }, 50); // Run frequently to override the widget's styles.

    // Cleanup function to clear the interval when the component unmounts.
    return () => clearInterval(intervalId);
  }, []);

  return (
    <Container as="section" id="review" className="py-5">
      <Card className="content-card shadow-sm">
        <Card.Body>
          <h2 className="text-center mb-4">What Our Patients Say</h2>
          <div
            className="elfsight-app-8c9274b6-c91c-432d-b861-ae87c74cf904"
            data-elfsight-app-lazy
          ></div>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default Review; 