import React from 'react';
import { Container, Button, Nav } from 'react-bootstrap';
import heroImage from '../assets/images/hero.jpeg';

const Hero = ({ data }) => {
  const { title, subtitle, branding_logo, links } = data;

  const heroStyle = {
    backgroundImage: `url(${heroImage})`,
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    minHeight: '100vh',
    display: 'flex',
    alignItems: 'center',
    position: 'relative',
  };

  const overlayStyle = {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: 'linear-gradient(135deg, rgba(254, 254, 254, 0.85) 25%, rgba(240, 242, 245, 0.85) 100%)',
    backdropFilter: 'blur(2px)',
  };

  const titleStyle = {
    fontSize: '40px',
    fontWeight: '700',
    lineHeight: '1.3em',
    letterSpacing: '1px',
    fontFamily: 'Poppins, Sans-serif',
    color: '#011C38',
  };

  const subtitleStyle = {
    fontSize: '20px',
    fontWeight: '700',
    lineHeight: '1.3em',
    letterSpacing: '1px',
    fontFamily: 'Poppins, Sans-serif',
    color: '#011C38',
    opacity: 0.9
  };

  return (
    <div id="home" style={heroStyle}>
      <div style={overlayStyle}></div>
      <Container style={{ position: 'relative', zIndex: 1 }} className="text-start">
        <div className="hero-logo-container">
          <img 
            src={require(`../assets/images/${branding_logo}`)} 
            alt="Klinik Kenanga" 
            className="hero-logo-img"
          />
        </div>
        <h1 style={titleStyle}>{title}</h1>
        <h2 style={subtitleStyle}>{subtitle}</h2>

        <div className="mt-4 d-none d-lg-block">
          <Button href="#services" variant="primary" size="lg" className="me-3">
            Our Services
          </Button>
          <Button href="#contact" variant="outline-secondary" size="lg" className="btn-outline-secondary-solid">
            Contact Us
          </Button>
        </div>

        <Nav className="d-lg-none mt-4 hero-action-buttons">
            <Button href={links.whatsapp} target="_blank" className="whatsapp-button action-button me-2 d-flex align-items-center">
                <i className="fab fa-whatsapp fa-lg"></i>
                <span style={{paddingLeft:'5px'}}>WhatsApp</span>
            </Button>
            <Button href={links.waze} target="_blank" className="waze-button action-button me-2 d-flex align-items-center">
                <i className="fab fa-waze fa-lg"></i>
                <span style={{paddingLeft:'5px'}}>Waze</span>
            </Button>
            <Button href={links.gmap} target="_blank" className="gmap-button action-button d-flex align-items-center">
                <i className="fab fa-google fa-lg"></i>
                <span style={{paddingLeft:'5px'}}>Google Maps</span>
            </Button>
        </Nav>
      </Container>
    </div>
  );
};

export default Hero; 