import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';

const Contact = ({ data, siteData }) => {
  if (!data || !siteData) return null;

  const { title, address, phone, email, hours } = data;
  const { links } = siteData;

  return (
    <Container as="section" id="contact" className="py-5">
      <Card className="content-card shadow-sm">
        <Card.Body>
          <Row>
            <Col md={6} className="mb-4 mb-md-0">
              <h2 className="text-center text-md-start mb-4">{title}</h2>
              <ul className="list-unstyled">
                <li className="mb-3 d-flex align-items-start">
                  <i className="fas fa-map-marker-alt fa-fw me-3 mt-1"></i>
                  <span>{address}</span>
                </li>
                <li className="mb-3 d-flex align-items-start">
                  <i className="fas fa-phone fa-fw me-3 mt-1"></i>
                  <a href={`tel:${phone}`}>{phone}</a>
                </li>
                <li className="mb-3 d-flex align-items-start">
                  <i className="fab fa-whatsapp fa-fw me-3 mt-1"></i>
                  <a href={links.whatsapp} target="_blank" rel="noopener noreferrer">Message us on WhatsApp</a>
                </li>
                <li className="mb-3 d-flex align-items-start">
                  <i className="fas fa-envelope fa-fw me-3 mt-1"></i>
                  <a href={`mailto:${email}`}>{email}</a>
                </li>
                <li className="mb-3 d-flex align-items-start">
                  <i className="fas fa-clock fa-fw me-3 mt-1"></i>
                  <div>
                    {hours.map((item, index) => (
                      <div key={index}>
                        <strong>{item.day}:</strong> {item.time}
                      </div>
                    ))}
                  </div>
                </li>
              </ul>
            </Col>
            <Col md={6}>
              <div className="map-responsive">
                <iframe
                  src={links.google_maps_embed}
                  width="600"
                  height="450"
                  style={{ border: 0 }}
                  allowFullScreen=""
                  loading="lazy"
                  title="Klinik Kenanga Location"
                ></iframe>
              </div>
            </Col>
          </Row>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default Contact; 